import { AxiosError } from "axios"
import { createContext, useContext, useState } from "react"
import { useMutation, UseMutationResult } from "react-query"
import { post } from "../../api/utils"
import useLocalStorage from "../../hooks/useLocalStorage"
import {
	IAuthContext,
	IAuthState,
	ILogoutFormData,
	ILogoutResponse
} from "./AuthContext.types"

//content for auth
const AuthContext = createContext<IAuthState | null>(null)

//hook to handle state and signin and sign up
function useAuthState() {
	const { storedValue } = useLocalStorage("access_token")
	const { storedValue: refreshToken } = useLocalStorage("refresh_token")
	const [authed, setAuthed] = useState(() => {
		if (storedValue && refreshToken) return true
		return false
	})

	//Mutation for Logout
	function logoutMutation(formdata: ILogoutFormData) {
		return post<ILogoutResponse>("/logout", formdata)
	}

	function signIn() {
		//login api request here.
		setAuthed(true)
	}
	async function logOut() {
		//api request here
		const mutation = await logoutMutation({
			access_token: storedValue,
			refresh_token: refreshToken
		})
		if (mutation.status == 200) {
			//set Auth to false
			//delete tokens
			setAuthed(false)
			localStorage.removeItem("access_token")
			localStorage.removeItem("refresh_token")
		}
		return mutation
	}

	return {
		authed,
		signIn,
		logOut,
		setAuthed
	}
}

export const AuthProvider = ({ children }: IAuthContext) => {
	const auth = useAuthState()
	return <AuthContext.Provider value={auth}>{children}</AuthContext.Provider>
}

//use this hook to consume the context
export const useAuthContext = () => {
	return useContext(AuthContext)
}
